import { useEffect, useState } from "react";
import { useAppDispatch } from "store/store";
import "./caloriealculator.scss";

const option = [
    {
        value: 1.2,
        description: " (  ,  )",
    },
    {
        value: 1.375,
        description: " (  45  1-3   )",
    },
    {
        value: 1.55,
        description:
            " (   30-60  1-4   )",
    },
    {
        value: 1.7,
        description: " (  5-7     60 )",
    },
    {
        value: 1.9,
        description:
            " (   60  2    6-7    )",
    },
];
export const Caloriealculator = () => {
    const [weight, setWeight] = useState<number>(0);
    const [height, setHeight] = useState<number>(0);
    const [age, setAge] = useState<number>(0);
    const [activeLevel, setActivLevel] = useState<number>(0);
    const [resCal, setResCal] = useState<number>(0);
    const dispatch = useAppDispatch();

    useEffect(() => {
        if (weight && height && age && activeLevel) {
            const res =
                (10 * weight + 6.25 * height - 5 * age - 161) * activeLevel;
            setResCal(Math.round(res));
        }
    }, [activeLevel, age, height, weight]);

    return (
        <>
            <section className='caloriealculator' id='caloriealculator'>
                <div className='container'>
                    <div className='title title__fz36 timer__title'>
                           
                    </div>
                    <div className='dividerr'></div>
                    <div className='caloriealculator__grid'>
                        <div className='caloriealculator__grid-item'>
                            <input
                                type='number'
                                className='caloriealculator__grid-item-num'
                                onChange={(e) => setWeight(+e.target.value)}
                            />
                            <div className='caloriealculator__grid-item-description'>
                                {",  "}
                            </div>
                        </div>
                        <div className='caloriealculator__grid-item'>
                            <input
                                type='number'
                                className='caloriealculator__grid-item-num'
                                onChange={(e) => setHeight(+e.target.value)}
                            />

                            <div className='caloriealculator__grid-item-description'>
                                {",  "}
                            </div>
                        </div>
                        <div className='caloriealculator__grid-item'>
                            <input
                                type='number'
                                className='caloriealculator__grid-item-num'
                                onChange={(e) => setAge(+e.target.value)}
                            />

                            <div className='caloriealculator__grid-item-description'>
                                {", "}
                            </div>
                        </div>
                        <div className='caloriealculator__grid-item'>
                            <select
                                className='caloriealculator__grid-item-num'
                                required
                                onChange={(e) => setActivLevel(+e.target.value)}
                            >
                                <option defaultValue=''></option>
                                {option?.map((el) => (
                                    <option
                                        key={el.description}
                                        value={el.value}
                                    >
                                        {el.description}
                                    </option>
                                ))}
                            </select>

                            <div className='caloriealculator__grid-item-description'>
                                {""}
                            </div>
                        </div>
                    </div>
                    <div className='caloriealculator__grid-result'>
                        <div className='caloriealculator__grid-result-number'>
                            {resCal}
                        </div>
                        <div className='caloriealculator__grid-result-description'>
                            {" ,  "}
                        </div>
                    </div>{" "}
                    <div className='caloriealculator__column'>
                        <ul>
                            <li>
                                <div className='caloriealculator__item'>
                                    <div className='caloriealculator__item-head'>
                                        <div className='caloriealculator__item-icon'></div>
                                        <div className='caloriealculator__item-location'>
                                              ,   
                                               10-15%
                                        </div>
                                    </div>
                                </div>
                            </li>{" "}
                            <li>
                                <div className='caloriealculator__item'>
                                    <div className='caloriealculator__item-head'>
                                        <div className='caloriealculator__item-icon'></div>

                                        <div className='caloriealculator__item-location'>
                                              ,   
                                               10-15%
                                        </div>
                                    </div>
                                </div>
                            </li>
                            <li>
                                <div className='caloriealculator__item'>
                                    <div className='caloriealculator__item-head'>
                                        <div className='caloriealculator__item-icon'></div>

                                        <div className='caloriealculator__item-location'>
                                               ,    
                                              250-400 
                                        </div>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </section>
        </>
    );
};
